import { FunctionalComponent, Fragment, h } from "preact";
import { useContext } from "preact/hooks";

import { Route, RouteContext, routeToString } from "../route";
import { Tool } from "../models";

export const ToolList: FunctionalComponent<{ tools: Tool[] }> = ({ tools }) => (
  <Fragment>
    {tools.map((tool, index) => (
      <Fragment>
        <a href={tool.url}>{tool.name}</a>
        {(index < tools.length - 2) ? ", " : ((index === tools.length - 2) ? ", and " : "")}
      </Fragment>
    ))}
  </Fragment>
);

interface LinkProps {
  route: Route;
  class?: string;
}

export const Link: FunctionalComponent<LinkProps> = (props) => {
  const { route, pushRoute } = useContext(RouteContext);

  return (
    <a
      class={`${routeToString(props.route) === routeToString(route) ? "active" : ""} ${props.class}`}
      onClick={() => pushRoute(props.route)}
    >
      {props.children}
    </a>
  );
};
