import { FunctionalComponent, h } from "preact";

interface InputNumbersProps {
  clickNumber: (num: number) => void;
  clearNumber: () => void;
}

const InputNumbers: FunctionalComponent<InputNumbersProps> = (props) => (
  <div class="sudoku-board">
    <div class="sudoku-row">
      {Array.from({ length: 9 }, (_, num) => (
        <div class="input-square" onClick={() => props.clickNumber(num + 1)}>
          <b>{(num + 1).toString()}</b>
        </div>
      ))}
      <div class="input-square" onClick={props.clearNumber}><b></b></div>
    </div>
  </div>
);

export default InputNumbers;
