import { Coordinates, SudokuSquare, Timing } from "../../models";

export const mapSquareAtCoords = ({ x, y }: Coordinates, squares: SudokuSquare[], mapper: (square: SudokuSquare) => SudokuSquare) =>
  squares.map((square, index) => (y * 9 + x) === index ? mapper(square) : square);

export const zipBoardWithCoords = (squares: SudokuSquare[]): [Coordinates, SudokuSquare][] =>
  squares.map((square, index) => [{ x: index % 9, y: Math.floor(index / 9) }, square])

export const boardIsComplete = (squares: SudokuSquare[]) =>
  squares.every((sq) =>
    sq.current.type_ === "pre-filled" ||
    (sq.current.type_ === "filled" && sq.current.value === sq.expected));

export const elapsedTime = (timing: Timing) => {
  if (!timing.started) return "0:00";

  const ending = timing.finished || timing.now;
  return readableSeconds(Math.floor((ending.getTime() - timing.started.getTime()) / 1000));
};

export const readableSeconds = (seconds: number) =>
  `${Math.floor(seconds / 60)}:${(seconds % 60).toString().padStart(2, "0")}`;
