import { FunctionalComponent, h } from "preact";

import { ResumeTab, resumeRoute } from "../route";
import { Link } from "../components/helpers";
import Work from "./tabs/work";
import Projects from "./tabs/projects";
import Skills from "./tabs/skills";
import Sidebar from "../components/sidebar";

const Resume: FunctionalComponent<{ tab: ResumeTab }> = ({ tab }) => (
  <div class="container">
    <div class="row row-reverse">
      <div class="col align-center">
        <h1 style={{ textAlign: "center", fontSize: "60px" }}>
          Sam Mohr
                </h1>
      </div>
    </div>
    <div class="row">
      <div class="col col-sm-12 col-md-4">
        <Sidebar />
      </div>
      <div class="col col-sm-12 col-md-8">
        <section>
          <div class="tabs-wrapper">
            <ul class="tabs">
              <li><Link class="tab" route={resumeRoute("work")}>Work</Link></li>
              <li><Link class="tab" route={resumeRoute("projects")}>Projects</Link></li>
              <li><Link class="tab" route={resumeRoute("skills")}>Skills</Link></li>
            </ul>
          </div>
          {tab === "work" ? <Work /> : (tab === "projects" ? <Projects /> : <Skills />)}
        </section>
      </div>
    </div>
  </div>
);

export default Resume;
