import { FunctionalComponent, h } from "preact";

import { Coordinates, SudokuSquare } from "../../models";
import { zipBoardWithCoords } from "./utils";

export const emptyBoard: SudokuSquare[] =
  Array(81).fill(undefined).map(() => ({ expected: 0, current: { type_: "empty" }, notes: new Set() }));

interface BoardProps {
  squares: SudokuSquare[];
  selected: Coordinates | null;
  showMistakes: boolean;
  clickSquare: (coords: Coordinates) => void;
}

export const Board: FunctionalComponent<BoardProps> = (props) => {
  const coordSquarePairs = zipBoardWithCoords(props.squares);

  return (
    <div class="sudoku-board">
      {Array.from({ length: 9 }, (_, rowIndex) => (
        <div class="sudoku-row">
          {coordSquarePairs
            .slice(rowIndex * 9, rowIndex * 9 + 9)
            .map(([coords, square]) => (
              <SquareFC {...props} square={square} coords={coords} />
            ))}
        </div>
      ))}
    </div>
  );
};

interface SquareFCProps {
  square: SudokuSquare;
  coords: Coordinates;
  selected: Coordinates | null;
  showMistakes: boolean;
  clickSquare: (coords: Coordinates) => void;
}

const SquareFC: FunctionalComponent<SquareFCProps> = (props) => (
  <div
    class="sudoku-square"
    style={{ ...squareBorder(props.coords), backgroundColor: squareBgColor(props) }}
    onClick={() => props.clickSquare(props.coords)}
  >
    {squareText(props.square)}
    <div class="sudoku-note">
      {(new Array(...props.square.notes)).map((n) => n.toString()).join(" ")}
    </div>
  </div>
);

const squareText = (square: SudokuSquare) =>
  square.current.type_ === "pre-filled" ? square.expected.toString()
    : (square.current.type_ === "filled" ? square.current.value.toString() : "");

const squareBgColor = (props: SquareFCProps) => {
  if (props.square.current.type_ === "pre-filled") return "#ebebeb";

  const visiblyWrong = props.square.current.type_ === "filled"
    && props.square.current.value !== props.square.expected
    && props.showMistakes;
  const isSelected = props.selected?.x === props.coords.x
    && props.selected?.y === props.coords.y;

  if (visiblyWrong) {
    return isSelected ? "#d98282" : "#e8aeae";
  } else {
    return isSelected ? "#adadad" : "#ffffff";
  }
};

const squareBorder = ({ x, y }: Coordinates) => ({
  border: "1px solid black",
  borderTopWidth: (y % 3) === 0 ? "2px" : (y === 0 ? "4px" : ""),
  borderBottomWidth: (y % 3) === 2 ? "2px" : (y === 8 ? "4px" : ""),
  borderLeftWidth: (x % 3) === 0 ? "2px" : (x === 0 ? "4px" : ""),
  borderRightWidth: (x % 3) === 2 ? "2px" : (x === 8 ? "4px" : ""),
});
