import { SudokuSquare } from "../../models";

export const randomPuzzle = () => {
  const randomIndex = Math.floor(Math.random() * starterPuzzles.length - 1);
  const starter = starterPuzzles[randomIndex];

  const squares: SudokuSquare[] = [];
  for (var index = 0; index < starter.length; index += 2) {
    const value = parseInt(starter[index]);
    const prefilled = starter[index + 1] === "t";

    squares.push({ expected: value, current: { type_: prefilled ? "pre-filled" : "empty" }, notes: new Set() });
  }

  return squares;
};

