import { FunctionalComponent, Fragment, h } from "preact";

import { allJobs } from "../../data";
import { Job } from "../../models";
import { ToolList } from "../../components/helpers";

const Work: FunctionalComponent = () => (
  <Fragment>
    {allJobs.map((job) => (<JobBlock job={job} />))}
  </Fragment>
);

const JobBlock: FunctionalComponent<{ job: Job }> = ({ job }) => (
  <div class="panel">
    <div class="panel-head">
      <p class="panel-title">
        <b>{job.company}</b>, {job.location}
      </p>
      <i class="align-right">from {job.startedAt} to {job.left}</i>
    </div>
    <div class="panel-body">
      <p>{job.description}</p>
      {job.accomplishments.length === 0 ? (
        <UnorderedList items={job.responsibilities} />
      ) : (
          <div class="row">
            <div class="col-sm-6">
              <span>My main accomplishments:</span>
              <br />
              <UnorderedList items={job.accomplishments} />
            </div>
            <div class="col-sm-6">
              <span>My daily responsibilities:</span>
              <br />
              <UnorderedList items={job.responsibilities} />
            </div>
          </div>
        )}
      <p>Tools used: <ToolList tools={job.tools} /></p>
    </div>
  </div>
);

const UnorderedList: FunctionalComponent<{ items: string[] }> = ({ items }) => (
  <ul>
    {items.map((item) => (<li><i>{item}</i></li>))}
  </ul>
);


export default Work;
