import { FunctionalComponent, h } from "preact";
import { useState, useCallback } from "preact/hooks";

import { RouteContext, Route, parseRoute, routeToString } from "../route";
import Resume from "../pages/resume";
import Sudoku from "../pages/sudoku";
import Reviews from "../pages/reviews";
import Recipes from "../pages/recipes";
import Header from "./header";

const App: FunctionalComponent = () => {
  console.log("first", location);

  const [menuOpen, setMenuOpen] = useState(false);
  const [route, setRoute] = useState(
    parseRoute("/" + location.hash, new URLSearchParams(location.search))
  );

  console.log("second", route, location);

  const pushRoute = useCallback(
    (r: Route) => {
      history.pushState(null, "", routeToString(r));
      setRoute(r);
    },
    [setRoute]
  );
  const replaceRoute = useCallback(
    (r: Route) => {
      history.replaceState(null, "", routeToString(r));
      setRoute(r);
    },
    [setRoute]
  );

  let currentPage: JSX.Element;
  if (route.page === "resume") {
    currentPage = <Resume tab={route.tab} />;
  } else if (route.page === "sudoku") {
    currentPage = <Sudoku />;
  } else if (route.page === "reviews") {
    currentPage = <Reviews />;
  } else {
    currentPage = <Recipes />;
  }

  return (
    <RouteContext.Provider value={{ route, pushRoute, replaceRoute }}>
      <div onClick={() => setMenuOpen(false)}>
        <Header menuOpen={menuOpen} setMenuOpen={setMenuOpen} />
        {currentPage}
      </div>
    </RouteContext.Provider>
  );
};

export default App;
