import { FunctionalComponent, h } from "preact";
import { useContext } from "preact/hooks";

import { RouteContext, resumeRoute } from "../route";
import { Link } from "../components/helpers";

interface HeaderProps {
  menuOpen: boolean;
  setMenuOpen: (isOpen: boolean) => void;
}

const Header: FunctionalComponent<HeaderProps> = ({ menuOpen, setMenuOpen }) => {
  const { route } = useContext(RouteContext);

  return (
    <nav style={{ background: "#a1cfff" }}>
      <div class="nav-container">
        <div class="nav-logo">
          <Link route={resumeRoute("work")}>Sam Mohr</Link>
        </div>
        <ul class="nav-links">
          <li><Link route={resumeRoute("work")} class={route.page === "resume" ? "active" : ""}>Resume</Link></li>
          <li><Link route={{ page: "sudoku" }}>Sudoku</Link></li>
          <li><Link route={{ page: "reviews" }} class={route.page === "reviews" ? "active" : ""}>Reviews</Link></li>
          <li><Link route={{ page: "recipes" }} class={route.page === "recipes" ? "active" : ""}>Recipes</Link></li>
        </ul>
        <a
          class="mobile-menu-toggle"
          onClick={(event) => {
            event.preventDefault();
            event.stopPropagation();
            setMenuOpen(!menuOpen);
          }}
        />
        <ul class="mobile-menu menu" style={{ display: menuOpen ? "block" : "none" }}>
          <li><Link route={resumeRoute("work")} class={route.page === "resume" ? "active" : ""}>Resume</Link></li>
          <li><Link route={{ page: "sudoku" }}>Sudoku</Link></li>
          <li><Link route={{ page: "reviews" }} class={route.page === "reviews" ? "active" : ""}>Reviews</Link></li>
          <li><Link route={{ page: "recipes" }} class={route.page === "recipes" ? "active" : ""}>Recipes</Link></li>
        </ul>
      </div>
    </nav>
  );
};

export default Header;
