import { FunctionalComponent, h } from "preact";
import { useState, useEffect, useCallback } from "preact/hooks";

import { SudokuSquare, Coordinates, Timing } from "../../models";
import { Board, emptyBoard } from "./board";
import { randomPuzzle } from "./starters";
import InputNumbers from "./input";
import { boardIsComplete, elapsedTime, mapSquareAtCoords } from "./utils";

const SudokuGame: FunctionalComponent = () => {
  const [squares, setSquares] = useState<SudokuSquare[] | null>(null);
  const [selected, setSelected] = useState<Coordinates | null>(null);
  const [notesMode, setNotesMode] = useState(false);
  const [showMistakes, setShowMistakes] = useState(false);
  const [timing, setTiming] = useState<Timing>({
    now: new Date(),
    started: null,
    finished: null
  });

  const pickNewPuzzle = useCallback(() => {
    setSquares(randomPuzzle());
    setTiming({ ...timing, started: timing.now, finished: null });
  }, [setSquares, setTiming, timing]);

  const clickSquare = useCallback((coords: Coordinates) => {
    if (squares && !boardIsComplete(squares)) {
      setSelected((selected?.x === coords.x && selected?.y === coords.y) ? null : coords);
    }
  }, [squares, setSelected, selected]);

  const clearSquare = useCallback(() => {
    if (squares && selected) {
      setSquares(mapSquareAtCoords(selected, squares, (square) =>
        notesMode ? { ...square, notes: new Set() } :
          (square.current.type_ === "filled" ?
            { ...square, current: { type_: "empty" } } : square)));
      setSelected(null);
    }
  }, [squares, selected, setSquares, setSelected, notesMode]);

  const inputNumber = useCallback((num: number) => {
    if (selected && squares && !boardIsComplete(squares)) {
      if (notesMode) {
        setSquares(mapSquareAtCoords(selected, squares, (square) => ({
          ...square,
          notes: square.notes.has(num) ?
            (new Set([...square.notes].filter((n) => n !== num))) :
            (new Set([...square.notes, num])),
        })));
      } else {
        const updatedSquares = mapSquareAtCoords(selected, squares, (square) => (
          square.current.type_ !== "pre-filled" ?
            { ...square, current: { type_: "filled", value: num } } :
            square
        ));
        setSquares(updatedSquares);
        setSelected(null);

        if (boardIsComplete(updatedSquares)) {
          setTiming({ ...timing, finished: timing.now });
        }
      }
    }
  }, [selected, squares, notesMode, setSquares, setSelected, setTiming, timing]);

  // update the current time once a second
  useEffect(() => {
    const interval = setInterval(() => {
      setTiming({ ...timing, now: new Date(timing.now.getTime() + 1000) });
    }, 1000);

    return () => clearInterval(interval);
  }, [timing, setTiming]);

  // control the board with the keyboard
  useEffect(() => {
    const onKeyDown = ({ key }: { key: string; }) => {
      const num = parseInt(key);
      if (num && num >= 1 && num <= 9) {
        inputNumber(num);
      } else if (key === " ") {
        setSelected(null);
      } else if (key === "c") {
        clearSquare();
      } else if (key === "n") {
        setNotesMode(!notesMode);
      } else if (key === "m") {
        setShowMistakes(!showMistakes);
      }
    }

    document.addEventListener("keydown", onKeyDown);
    return () => {
      document.removeEventListener("keydown", onKeyDown);
    }
  }, [inputNumber, setSelected, clearSquare, setNotesMode, setShowMistakes, notesMode, showMistakes]);

  return (
    <div class="row">
      <div class="col col-sm-12 col-md-8">
        <Board
          squares={squares || emptyBoard}
          selected={selected}
          showMistakes={showMistakes}
          clickSquare={clickSquare}
        />
        <br />
        <InputNumbers clickNumber={inputNumber} clearNumber={clearSquare} />
      </div>
      <div class="col col-sm-12 col-md-4 align-center">
        <p class={`sudoku-timer${timing.finished ? " finished" : ""}`}>
          {elapsedTime(timing)}
        </p>
        <fieldset>
          <legend>Options</legend>
          <div class="form-control">
            <label>
              <input
                type="checkbox"
                onInput={() => setShowMistakes(!showMistakes)}
                checked={showMistakes}
              />
                            Show Mistakes
                        </label>
          </div>
          <div class="form-control">
            <label>
              <input
                type="checkbox"
                onInput={() => setNotesMode(!notesMode)}
                checked={notesMode}
              />
                            Notes Mode
                        </label>
          </div>
          <div class="form-control">
            <button class="align-right" onClick={pickNewPuzzle} style={{ marginBottom: "0" }}>
              New Game
                        </button>
          </div>
        </fieldset>
        <ControlsTable />
      </div>
    </div>
  );
};

const ControlsTable: FunctionalComponent = () => (
  <table class="controls">
    <thead>
      <tr><th>Key</th><th>Action</th></tr>
    </thead>
    <tbody>
      <tr>
        <td><i>1-9</i></td>
        <td>input a number</td>
      </tr>
      <tr>
        <td><i>n</i></td>
        <td>toggle notes</td>
      </tr>
      <tr>
        <td><i>m</i></td>
        <td>show mistakes</td>
      </tr>
      <tr>
        <td><i>c</i></td>
        <td>clear square</td>
      </tr>
      <tr>
        <td><i>space</i></td>
        <td>clear selection</td>
      </tr>
    </tbody>
  </table>
);

export default SudokuGame;
