import { FunctionalComponent, Fragment, h } from "preact";
import { useState, useEffect, useMemo, useCallback, useContext } from "preact/hooks";

import { Review } from "../models";
import { RouteContext, ReviewsRoute } from "../route";

const Reviews: FunctionalComponent = () => {
  const { route, replaceRoute } = useContext(RouteContext);
  const { title, unreviewed, year, reviewed } = route as ReviewsRoute;

  const [reviews, setReviews] = useState<Review[]>([]);
  const [status, setStatus] = useState("loading");

  const setTitle = useCallback((title: string) => {
    replaceRoute({ page: "reviews", title, unreviewed, year, reviewed });
  }, [unreviewed, year, reviewed, replaceRoute]);

  const setYear = useCallback((year: number | null) => {
    replaceRoute({ page: "reviews", title, unreviewed, year: year || undefined, reviewed });
  }, [title, unreviewed, reviewed, replaceRoute]);

  const setUnreviewed = useCallback((unreviewed: boolean) => {
    replaceRoute({ page: "reviews", title, unreviewed, year, reviewed });
  }, [title, year, reviewed, replaceRoute]);

  const setReviewed = useCallback((reviewed: Date | null) => {
    const dateReviewed = (reviewed && !isNaN(reviewed.getTime())) ? reviewed.toISOString().split("T")[0] : undefined;
    replaceRoute({ page: "reviews", title, unreviewed, year, reviewed: dateReviewed });
  }, [title, unreviewed, year, replaceRoute]);

  useEffect(() => {
    const url = "https://api.mohr.codes/";
    const query = "{ reviews { title year rating review reviewed } }";

    fetch(url, {
      method: "post",
      mode: "cors",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({ query, variables: null })
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.errors?.length > 0) {
          setStatus(data.errors[0].message);
        } else {
          setReviews(data.data.reviews);
          setStatus("loaded");
        }
      })
      .catch((error) => setStatus(`${error}`));
  }, [setReviews, setStatus]);

  const visibleReviews = useMemo(() => {
    const lowerTitle = (title || "").toLowerCase();

    return reviews.filter((review) =>
      (unreviewed ? review.rating === null : true)
      && (reviewed ? (review.reviewed ? new Date(review.reviewed) < new Date(reviewed) : false) : true)
      && review.title.toLowerCase().includes(lowerTitle)
      && (year ? review.year === year : true));
  }, [title, unreviewed, year, reviewed, reviews]);

  return (
    <div class="container container-small">
      <div class="row">
        <div class="col align-center">
          <h1>Reviews</h1>
          <section>
            {status === "loading" ? (
              <div class="progress-bar striped animated">
                <span class="progress-bar-blue" style={{ width: "100%" }} />
              </div>
            ) : status !== "loaded" ? (
              <p class="alert alert-danger">{status}</p>
            ) : (
                  <Fragment>
                    <FilterReviews
                      title={title || ""}
                      setTitle={setTitle}
                      year={year || null}
                      setYear={setYear}
                      reviewed={reviewed ? new Date(reviewed) : null}
                      setReviewed={setReviewed}
                      unreviewed={unreviewed || false}
                      setUnreviewed={setUnreviewed}
                    />
                    {visibleReviews.length === 0 ? (
                      <p>
                        <i>
                          {(title || year || unreviewed || reviewed) ?
                            "No reviews found for the given query." :
                            "No reviews available."
                          }
                        </i>
                      </p>
                    ) : (
                        <div>
                          <strong>{`${visibleReviews.length} review${visibleReviews.length === 1 ? "" : "s"} found:`}</strong>
                          {visibleReviews.map((review) => <ReviewCard review={review} />)}
                        </div>
                      )}
                  </Fragment>
                )}
          </section>
        </div>
      </div>
    </div>
  );
};

interface FilterReviewsProps {
  title: string;
  setTitle: (title: string) => void;
  year: number | null;
  setYear: (year: number | null) => void;
  reviewed: Date | null;
  setReviewed: (reviewed: Date | null) => void;
  unreviewed: boolean;
  setUnreviewed: (unreviewed: boolean) => void;
}

const FilterReviews: FunctionalComponent<FilterReviewsProps> = ({
  title,
  setTitle,
  year,
  setYear,
  reviewed,
  setReviewed,
  unreviewed,
  setUnreviewed
}) => (
  <fieldset>
    <legend>Filter Reviews</legend>
    <div class="form-control-group">
      <div class="form-control grow-3x">
        <label>By Title</label>
        <input
          type="text"
          placeholder="Shrek"
          value={title}
          onInput={(event) => setTitle(event.currentTarget.value)}
        />
      </div>
      <div class="form-control">
        <label>Unreviewed</label>
        <button onClick={() => setUnreviewed(!unreviewed)}>
          {unreviewed ? "Only Unreviewed" : "All Movies"}
        </button>
      </div>
    </div>
    <div class="form-control-group">
      <div class="form-control">
        <label>By Release Year</label>
        <input
          type="number"
          placeholder="2099"
          value={year || ""}
          onInput={(event) => setYear(parseInt(event.currentTarget.value) || null)}
        />
      </div>
      <div class="form-control">
        <label>By Review Date</label>
        <input
          type="date"
          value={reviewed ? reviewed.toISOString().split("T")[0] : ""}
          onInput={(event) => setReviewed(new Date(event.currentTarget.value))}
        />
      </div>
    </div>
  </fieldset>
);

const ReviewCard: FunctionalComponent<{ review: Review }> = ({ review }) => (
  <div class="card">
    <h3 class="card-title">{review.title}{review.year && ` (${review.year})`}</h3>
    {review.rating ? (
      <Fragment>
        <h5>
          <span class={`stars-container stars-${Math.round(review.rating * 2) * 5}`}></span>
        </h5>
        {review.review ? (
          <p>{review.review}{review.reviewed && <i> - reviewed on {review.reviewed}</i>}</p>
        ) : (
            <p><i>No review</i></p>
          )}
      </Fragment>
    ) : (
        <i>not yet rated</i>
      )}
  </div>
);

export default Reviews;
