import { FunctionalComponent, Fragment, h } from "preact";
import { useState, useEffect, useMemo, useCallback, useContext } from "preact/hooks";

import { Recipe, Ingredient, Nutrition, IngredientsForComponent } from "../models";
import { RouteContext, RecipesRoute } from "../route";

const Recipes: FunctionalComponent = () => {
  const { route, replaceRoute } = useContext(RouteContext);
  const { name, tags } = route as RecipesRoute;

  const [recipes, setRecipes] = useState<Recipe[]>([]);
  const [allTags, setAllTags] = useState<string[]>([]);
  const [status, setStatus] = useState("loading");

  const [expandedRecipes, setExpandedRecipes] = useState<string[]>([]);

  const toggleRecipeExpanded = useCallback((recipeName: string) => {
    if (expandedRecipes.includes(recipeName)) {
      setExpandedRecipes(expandedRecipes.filter((name) => name !== recipeName));
    } else {
      setExpandedRecipes([...expandedRecipes, recipeName]);
    }
  }, [setExpandedRecipes, expandedRecipes]);

  const setName = useCallback((name: string) => {
    replaceRoute({ page: "recipes", tags, name });
  }, [tags, replaceRoute]);

  const setTags = useCallback((tags: string[]) => {
    replaceRoute({ page: "recipes", tags, name });
  }, [name, replaceRoute]);

  useEffect(() => {
    const url = "https://api.mohr.codes/";
    const query = `{
          recipeTags
          recipes {
              name tags image links steps
              ingredients {
                  component
                  ingredients {
                      item quantity notes substitutes optional
                  }
              }
              nutrition {
                  servings servingSize calories fat carbs netCarbs protein fiber
              }
          }
      }`;

    fetch(url, {
      method: "post",
      mode: "cors",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({ query, variables: null })
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.errors?.length > 0) {
          setStatus(data.errors[0].message);
        } else {
          setRecipes(data.data.recipes);
          setAllTags(data.data.recipeTags);
          setStatus("loaded");
        }
      })
      .catch((error) => setStatus(`${error}`));
  }, [setAllTags, setRecipes, setStatus]);

  const visibleRecipes = useMemo(() => {
    const lowerName = (name || "").toLowerCase();

    return recipes.filter((recipe) =>
      (tags || []).every((tag) => recipe.tags.includes(tag))
      && recipe.name.toLowerCase().includes(lowerName));
  }, [name, tags, recipes]);

  return (
    <div class="container container-small">
      <div class="row">
        <div class="col align-center">
          <h1>Recipes</h1>
          <section>
            {status === "loading" ? (
              <div class="progress-bar striped animated">
                <span class="progress-bar-blue" style={{ width: "100%" }} />
              </div>
            ) : status !== "loaded" ? (
              <p class="alert alert-danger">{status}</p>
            ) : (
                  <Fragment>
                    <FilterRecipes
                      name={name || ""}
                      setName={setName}
                      tags={tags || []}
                      allTags={allTags}
                      setTags={setTags}
                    />
                    {visibleRecipes.length === 0 ? (
                      <p>
                        <i>
                          {(name || tags?.length) ?
                            "No recipes found for the given query." :
                            "No recipes available."
                          }
                        </i>
                      </p>
                    ) : (
                        <div>
                          <strong>
                            {`${visibleRecipes.length} recipe${visibleRecipes.length === 1 ? "" : "s"
                              } found:`}
                          </strong>
                          {visibleRecipes.map((recipe) => <RecipePanel
                            recipe={recipe}
                            expanded={expandedRecipes.includes(recipe.name)}
                            toggleRecipeExpanded={toggleRecipeExpanded}
                          />)}
                        </div>
                      )}
                  </Fragment>
                )}
          </section>
        </div>
      </div>
    </div>
  );
};

interface FilterRecipesProps {
  name: string;
  setName: (name: string) => void;
  tags: string[];
  allTags: string[];
  setTags: (tags: string[]) => void;
}

const FilterRecipes: FunctionalComponent<FilterRecipesProps> = ({
  name,
  setName,
  tags,
  allTags,
  setTags
}) => (
  <fieldset>
    <legend>Filter Recipes</legend>
    <div class="form-control">
      <label>By Name</label>
      <input
        type="text"
        placeholder="Meatball Mania"
        value={name}
        onInput={(event) => setName(event.currentTarget.value)}
      />
    </div>
    <div class="form-control">
      <label>By Tag</label>
      <ul class="tags">
        {allTags.map((tag) => (
          <li
            class={`tag tag-rounded${tags.includes(tag) ? " tag-green" : ""}`}
            onClick={() => {
              if (tags.includes(tag)) {
                setTags(tags.filter((t) => t !== tag));
              } else {
                setTags([...tags, tag]);
              }
            }}
          >
            {tag}
          </li>
        ))}
      </ul>
    </div>
  </fieldset>
);

interface RecipePanelProps {
  recipe: Recipe;
  expanded: boolean;
  toggleRecipeExpanded: (recipeName: string) => void;
}

const RecipePanel: FunctionalComponent<RecipePanelProps> = ({
  recipe,
  expanded,
  toggleRecipeExpanded
}) => (
  <div class="panel">
    <div class="panel-head">
      <p class="panel-title">{recipe.name}</p>
      <ul class="tags align-right">
        {recipe.tags.map((tag) => (
          <li class="tag tag-rounded">{tag}</li>
        ))}
      </ul>
    </div>
    {expanded && (
      <div class="panel-body align-left">
        {recipe.image && (
          <section style={{ paddingTop: "0" }}>
            <img style={{ width: "100%" }} src={recipe.image} />
          </section>
        )}
        <h4>Ingredients:</h4>
        <IngredientList ingredients={recipe.ingredients} />
        <h4>Steps:</h4>
        <ol>
          {recipe.steps.map((step) => (
            <li>{step}</li>
          ))}
        </ol>
        {recipe.nutrition && <NutritionInfo nutrition={recipe.nutrition} />}
        {recipe.links.length > 0 && (
          <Fragment>
            <h4>Links:</h4>
            <ul>
              {recipe.links.map((link) => (
                <li><a href={link}>{link}</a></li>
              ))}
            </ul>
          </Fragment>
        )}
      </div>
    )}
    <div class="panel-footer">
      <button onClick={() => toggleRecipeExpanded(recipe.name)}>
        {expanded ? "Hide" : "Expand"}
      </button>
    </div>
  </div>
);

const IngredientList: FunctionalComponent<{ ingredients: IngredientsForComponent[] }> = ({
  ingredients
}) => (ingredients.length === 1) ? (
  <ul>
    {ingredients[0].ingredients.map((ingredient) => (
      <IngredientListItem ingredient={ingredient} />
    ))}
  </ul>
) : (
      <Fragment>
        {ingredients.map((group) => (
          <Fragment>
            <h5>For the {group.component}:</h5>
            <ul>
              {group.ingredients.map((ingredient) => (
                <IngredientListItem ingredient={ingredient} />
              ))}
            </ul>
          </Fragment>
        ))}
      </Fragment>
    );

const IngredientListItem: FunctionalComponent<{ ingredient: Ingredient }> = ({ ingredient }) => (
  <li>
    {ingredient.item}
    {ingredient.quantity && ` - ${ingredient.quantity}`}
    {ingredient.optional && (<i> (optional) </i>)}
    {ingredient.notes && (<i> ({ingredient.notes}) </i>)}
    {ingredient.substitutes.length > 0 && (
      <i> [can substitute with {ingredient.substitutes.join(" / ")}] </i>
    )}
  </li>
);

const NutritionInfo: FunctionalComponent<{ nutrition: Nutrition }> = ({ nutrition }) => (
  <Fragment>
    <h4>Nutrition:</h4>
    <table style={{ lineHeight: "0.5" }}>
      <tbody>
        {nutrition.servings && (
          <tr><td>Servings</td><td>{nutrition.servings}</td></tr>
        )}
        {nutrition.servingSize && (
          <tr><td>Serving Size</td><td>{nutrition.servingSize}</td></tr>
        )}
        {nutrition.calories && (
          <tr><td>Calories</td><td>{nutrition.calories} cal</td></tr>
        )}
        {nutrition.carbs && (
          <tr><td>Carbs</td><td>{nutrition.carbs} g</td></tr>
        )}
        {nutrition.fiber && (
          <tr><td>Fiber</td><td>{nutrition.fiber} g</td></tr>
        )}
        {nutrition.netCarbs && (
          <tr><td>Net Carbs</td><td>{nutrition.netCarbs} g</td></tr>
        )}
        {nutrition.fat && (
          <tr><td>Fat</td><td>{nutrition.fat} g</td></tr>
        )}
        {nutrition.protein && (
          <tr><td>Protein</td><td>{nutrition.protein} g</td></tr>
        )}
      </tbody>
    </table>
  </Fragment>
);

export default Recipes;
